import request from '@/helpers/request'
import {friendlyDate} from '@/helpers/util.js'
import dayjs from 'dayjs'

const URL = {
    GET:'/notes/trash',
    REVERT:'/notes/:noteId/revert',
    DELETE:'/notes/:noteId/confirm'
}

export default {
    getAll(){
        return new Promise((resolve,reject)=>{
            request(URL.GET)
                .then(res =>{
                    res.data = res.data.sort((note1,note2) => note1.updatedAt < note2.updatedAt ? 1 : -1)
                    res.data.forEach(note =>{
                        note.updatedAt = friendlyDate(note.updatedAt)
                        note.createdAt = dayjs(note.createdAt).locale('zh-cn').format('YYYY/MM/DD/HH:mm')
                    })
                    // console.log(res.data)
                    resolve(res)
                }).catch(err =>{reject(err)})
        })
    },
    deleteNote({noteId}){
        return request(URL.DELETE.replace(':noteId',noteId),'DELETE')
    },
    revertNote({noteId}){
        return request(URL.REVERT.replace(':noteId',noteId),'PATCH')
    },

}